import { Button } from "@/components/ui/button";
import { Github, Linkedin, Mail, Phone } from "lucide-react";

const contactInfo = [
  { icon: <Mail />, text: "hello@personacanvas.dev", href: "mailto:hello@personacanvas.dev" },
  { icon: <Phone />, text: "+1 (234) 567-890", href: "tel:+1234567890" },
  { icon: <Linkedin />, text: "linkedin.com/in/persona-canvas", href: "#" },
  { icon: <Github />, text: "github.com/persona-canvas", href: "#" },
];

export function Contact() {
  return (
    <section id="contact" className="container py-20 md:py-24">
      <h2 className="text-3xl font-bold text-center mb-2 font-headline">Get In Touch</h2>
      <p className="text-muted-foreground text-center max-w-2xl mx-auto mb-12">
        I'm currently open to new opportunities and collaborations. Feel free to reach out!
      </p>
      <div className="max-w-sm mx-auto space-y-4">
        {contactInfo.map((item, index) => (
          <a
            key={index}
            href={item.href}
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center p-4 rounded-lg bg-muted/50 dark:bg-card hover:bg-muted dark:hover:bg-muted/50 transition-colors group"
          >
            <div className="text-primary mr-4 shrink-0">
              {item.icon}
            </div>
            <span className="text-muted-foreground group-hover:text-foreground transition-colors">{item.text}</span>
          </a>
        ))}
      </div>
    </section>
  );
}
